import { dispatch, getState } from '../store/config/store';
import loadingCreater from '../store/loadingActionCreater';

export const showLoading = () => {
  if (getState().loading?.isLoading) {
    return;
  }

  dispatch(loadingCreater.showLoading());
};

export const hideLoading = () => {
  if (!getState().loading?.isLoading) {
    return;
  }

  dispatch(loadingCreater.hideLoading());
};

const loadingService = { showLoading, hideLoading };

export default loadingService;
