import _ from 'lodash';
import { utils, BigNumber } from 'ethers';

function computedLiquidity(reserve: [BigNumber, BigNumber]): number {
  const reserveQuote = Number(utils.formatEther(reserve[0]));

  if (!reserveQuote) return 0;

  return reserveQuote;
}

function computedTokenReservePricePerQuote(
  reserve: [BigNumber, BigNumber],
  tokendecimals: number = 18
): number {
  const reserveQuote = Number(utils.formatEther(reserve[0]));
  const reserveToken = Number(utils.formatUnits(reserve[1], tokendecimals));

  if (!reserveQuote || !reserveToken) return 0;

  const reservePrice: number = reserveQuote / reserveToken;

  return reservePrice;
}

function convertToInternationalCurrencySystem(currency: number): string {
  const abs: number = Math.abs(Number(currency));
  const currencySystem: string =
    // Nine Zeroes for Billions
    abs >= 1.0e9
      ? _.round(abs / 1.0e9, 3) + 'B'
      : // Six Zeroes for Millions
      abs >= 1.0e6
      ? _.round(abs / 1.0e6, 2) + 'M'
      : // Three Zeroes for Thousands
      abs >= 1.0e3
      ? _.round(abs / 1.0e3, 1) + 'K'
      : _.round(abs).toString();

  return currencySystem;
}

function convertBalanceToInternationalCurrencySystem(currency: number): string {
  const abs: number = Math.abs(Number(currency));
  const currencySystem: string =
    // Nine Zeroes for Billions
    abs >= 1.0e9
      ? _.round(abs / 1.0e9, 2) + 'B'
      : // Six Zeroes for Millions
      abs >= 1.0e6
      ? _.round(abs / 1.0e6, 2) + 'M'
      : // Five Zeroes for Thousands
      abs >= 1.0e5
      ? _.round(abs / 1.0e3) + 'K'
      : abs >= 1.0e3
      ? _.round(abs, 2).toString()
      : abs >= 1.0e1
      ? _.round(abs, 3).toString()
      : _.round(abs, 6).toString();

  return currencySystem;
}

function roundCurrency(currency: number): string {
  const abs: number = Math.abs(Number(currency));
  let roundedCurrency;

  if (abs >= 1) {
    const decimal: number = Math.max(5 - Math.floor(abs).toString().length, 2);

    roundedCurrency = _.round(abs, decimal);
  } else {
    const decimal: number = Math.floor(1 / (abs % 1)).toString().length + 4;

    roundedCurrency = _.round(abs, decimal);
  }

  return roundedCurrency.toString();
}

function buildBurnPercent(burn?: number): string {
  if (_.isNil(burn)) {
    return '';
  }

  if (burn === -1) {
    return '?';
  }

  return burn + '%';
}

const helper = {
  computedLiquidity,
  computedTokenReservePricePerQuote,
  convertToInternationalCurrencySystem,
  convertBalanceToInternationalCurrencySystem,
  roundCurrency,
  buildBurnPercent,
};

export default helper;
