import { providers, Contract } from 'ethers';
import { ABI_BEP20_FACTORY, ABI_BEP20_PROXY } from '../config/abiBep20Config';
import {
  BSC_NODE,
  PANCAKE_FACTORY,
  APE_FACTORY,
  BEP20_PROXY,
} from '../config/web3Config';

export const bscProvider = new providers.JsonRpcProvider(BSC_NODE);

export const pancakeFactory: Contract = new Contract(
  PANCAKE_FACTORY,
  ABI_BEP20_FACTORY,
  bscProvider
);

export const apeFactory: Contract = new Contract(
  APE_FACTORY,
  ABI_BEP20_FACTORY,
  bscProvider
);

export const bep20Proxy: Contract = new Contract(
  BEP20_PROXY,
  ABI_BEP20_PROXY,
  bscProvider
);
