import { PureComponent, ReactElement, ChangeEvent } from 'react';
import browserHelper from '../../../helper/browserHelper';
import style from './InputText.module.scss';

interface IStoreProps {}
interface IHOCProps {}
interface IOwnProps {
  inputMode:
    | 'text'
    | 'search'
    | 'none'
    | 'tel'
    | 'url'
    | 'email'
    | 'numeric'
    | 'decimal';
  type: 'text' | 'password';
  className: string;
  isShowClear: boolean;
  placeholder: string;
  value: string;
  onChange: (v: string) => void;
}
interface IOwnState {}

class InputText extends PureComponent<
  IStoreProps & IHOCProps & IOwnProps,
  IOwnState
> {
  static defaultProps = {
    inputMode: 'text',
    type: 'text',
    className: '',
    isShowClear: false,
    placeholder: '',
    value: '',
    onChange: () => {},
  };

  state: IOwnState;

  constructor(props: IOwnProps & IHOCProps & IStoreProps) {
    super(props);

    this.state = {};

    this.onChange = this.onChange.bind(this);
    this.onClear = this.onClear.bind(this);
  }

  onChange(e: ChangeEvent<HTMLInputElement>) {
    this.props.onChange(e.target.value);
  }

  onClear() {
    this.props.onChange('');
  }

  render(): ReactElement {
    const { inputMode, type, className, isShowClear, placeholder, value } =
      this.props;

    return (
      <div className='position-relative'>
        <input
          inputMode={inputMode}
          type={type}
          className={'fk-input' + (className ? ` ${className}` : '')}
          placeholder={placeholder}
          value={value}
          onChange={this.onChange}
        />
        {isShowClear && !!value && (
          <button
            className={
              'fk-button-icon ' +
              style.btnIcon +
              (browserHelper.iOS() ? ` ${style.btnIconIOS}` : '')
            }
            onClick={this.onClear}
          >
            <i className='bi bi-x-lg'></i>
          </button>
        )}
      </div>
    );
  }
}

export default InputText;
