import _ from 'lodash';
import { toast } from 'react-toastify';
import { CHUNK_DATA_NUMBER } from '../config/appConfig';
import { bep20Proxy } from './web3Service';
import { getState, dispatch } from '../store/config/store';
import tokenAction from '../store/tokenActionCreater';
import { ITokenDetail } from '../model/tokenModel';

export async function getTokenInfos() {
  let tokens: ITokenDetail[] = getState().tokens;
  const tokenAddress: string[] = tokens.map(
    (token: ITokenDetail) => token.address
  );

  let infos: [string[], number[]] = [[], []];
  let isRequestError = false;

  try {
    infos = await getChunkTokenInfos(tokenAddress);
  } catch (error) {
    isRequestError = true;
    toast.error('Get token info failure!', { autoClose: 500 });
    console.log(error);
  }

  if (isRequestError) {
    return;
  }

  tokens = tokens.map(
    (token: ITokenDetail, index: number): ITokenDetail => ({
      ...token,
      symbol: infos[0][index],
      decimals: infos[1][index],
    })
  );

  dispatch(tokenAction.getTokenInfo(tokens));
}

async function getChunkTokenInfos(
  tokenAddress: string[]
): Promise<[string[], number[]]> {
  const chunkAddress: string[][] = _.chunk(tokenAddress, CHUNK_DATA_NUMBER);
  let chunkSymbolPromises: Promise<any>[] = [];
  let chunkDecimalPromises: Promise<any>[] = [];

  _.each(chunkAddress, (address: string[]) => {
    chunkSymbolPromises.push(bep20Proxy.getSymbols(address));
    chunkDecimalPromises.push(bep20Proxy.getDecimals(address));
  });

  const chunkDatas = await Promise.all([
    ...chunkSymbolPromises,
    ...chunkDecimalPromises,
  ]);
  const infos: [string[], number[]] = [
    _.flatten(_.slice(chunkDatas, 0, _.size(chunkDatas) / 2) as string[][]),
    _.flatten(_.slice(chunkDatas, _.size(chunkDatas) / 2) as number[][]),
  ];

  return infos;
}
