import { PureComponent, ReactElement } from 'react';
import _ from 'lodash';
import { VARIATION_PERCENT_HIGHLIGHT } from '../../../config/appConfig';
import style from './Variation.module.scss';

interface IStoreProps {}
interface IHOCProps {}
interface IOwnProps {
  variation: number;
  division: number;
}
interface IOwnState {}

class Variation extends PureComponent<
  IOwnProps & IHOCProps & IStoreProps,
  IOwnState
> {
  constructor(props: IOwnProps & IHOCProps & IStoreProps) {
    super(props);

    this.getStyle = this.getStyle.bind(this);
    this.getVariationText = this.getVariationText.bind(this);
  }

  private getStyle(): string {
    if (_.isNil(this.props.variation)) return style.normal;

    const variation = _.round(this.props.variation, 2);

    if (
      variation > -VARIATION_PERCENT_HIGHLIGHT &&
      variation < VARIATION_PERCENT_HIGHLIGHT
    ) {
      return style.normal;
    } else if (variation >= VARIATION_PERCENT_HIGHLIGHT) {
      return style.manyUp;
    } else {
      return style.manyDown;
    }
  }

  private getVariationText(): string {
    if (_.isNil(this.props.variation)) return 'NA';

    const variation = _.round(this.props.variation, 2);
    const division = _.round(this.props.division, 1);
    let variationText: string;

    if (variation === 0) {
      variationText = variation + '%';
    } else if (variation > 0) {
      variationText = '+' + variation + '%';
    } else {
      variationText =
        variation +
        '%' +
        (variation <= -VARIATION_PERCENT_HIGHLIGHT ? `  ${division}` : '');
    }

    return variationText;
  }

  render(): ReactElement {
    return (
      <span className={style.wrap + ' ' + this.getStyle()}>
        {this.getVariationText()}
      </span>
    );
  }
}

export default Variation;
