import { toast } from 'react-toastify';
import { IBrowserNotification } from '../model/notificationModel';
import logoBep20 from '../asset/logo/bep20.png';

export function checkBrowserNotification() {
  if (!('Notification' in window)) {
    toast.warn('This browser does not support desktop notification!');

    return;
  }

  if (Notification.permission !== 'granted') Notification.requestPermission();
}

export function notifyVariationPrice(
  browserNotifications: IBrowserNotification[]
) {
  if (!('Notification' in window)) {
    browserNotifications.forEach(
      (browserNotification: IBrowserNotification) => {
        toast.info(`${browserNotification.title}`);
      }
    );
  } else {
    if (Notification.permission === 'granted') {
      pushBrowserNotification(browserNotifications, logoBep20);
    } else {
      Notification.requestPermission().then((permission: string) => {
        if (permission === 'granted') {
          pushBrowserNotification(browserNotifications, logoBep20);
        }
      });
    }
  }
}

function pushBrowserNotification(
  browserNotifications: IBrowserNotification[],
  defaultIcon?: string
) {
  browserNotifications.forEach((browserNotification: IBrowserNotification) => {
    const notification = new Notification(browserNotification.title, {
      icon: browserNotification.icon || defaultIcon,
      body: browserNotification.body,
    });

    notification.onclick = function (e) {
      e.preventDefault();
      window.open(
        `https://poocoin.app/tokens/${browserNotification.address}`,
        '_blank'
      );
    };
  });
}
