import _ from 'lodash';
import { utils, BigNumber } from 'ethers';
import { toast } from 'react-toastify';
import { CHUNK_DATA_NUMBER } from '../config/appConfig';
import {
  BUSD_BEP20,
  USDT_BEP20,
  USDC_BEP20,
  BNB_BEP20,
} from '../config/tokenBep20Config';
import {
  PANCAKE_FACTORY,
  APE_FACTORY,
  INVALID_ADDRESS,
} from '../config/web3Config';
import tokenHelper from '../helper/tokenHelper';
import { bep20Proxy } from './web3Service';
import { dispatch, getState } from '../store/config/store';
import quoteTokenAction from '../store/quoteTokenActionCreater';
import tokenAction from '../store/tokenActionCreater';
import { DApp, IPool, ITokenDetail } from '../model/tokenModel';

type TPoolAddressReserve = [
  string[],
  string[],
  [BigNumber, BigNumber][],
  [BigNumber, BigNumber][]
];

type AddressReserve = {
  address: string[];
  reserve: [BigNumber, BigNumber][];
};

type TCategorizeAddressReserve = {
  busd: AddressReserve;
  usdt: AddressReserve;
  usdc: AddressReserve;
  bnb: AddressReserve;
};

type TCategorizePool = {
  pancake: TCategorizeAddressReserve;
  ape: TCategorizeAddressReserve;
};

export async function getPools() {
  let tokens: ITokenDetail[] = getState().tokens;
  let busdAdressPairs: [string, string][] = [];
  let usdtAdressPairs: [string, string][] = [];
  let usdcAdressPairs: [string, string][] = [];
  let bnbAdressPairs: [string, string][] = [];
  let adressPairs: [string, string][] = [];

  tokens.forEach((token: ITokenDetail) => {
    busdAdressPairs.push([BUSD_BEP20.address, token.address]);
    usdtAdressPairs.push([USDT_BEP20.address, token.address]);
    usdcAdressPairs.push([USDC_BEP20.address, token.address]);
    bnbAdressPairs.push([BNB_BEP20.address, token.address]);
  });

  adressPairs = [
    ...busdAdressPairs,
    ...usdtAdressPairs,
    ...usdcAdressPairs,
    ...bnbAdressPairs,
  ];

  let poolAddressReserve: TPoolAddressReserve = [[], [], [], []];
  let isRequestError = false;

  try {
    poolAddressReserve = await getChunkPools(adressPairs);
  } catch (error) {
    isRequestError = true;
    toast.error('Get token pool failure!');
    console.log(error);
  }

  if (isRequestError) {
    return;
  }

  const bnbReservePrice = tokenHelper.computedTokenReservePricePerQuote(
    _.last(poolAddressReserve[2]) as [BigNumber, BigNumber]
  );
  dispatch(quoteTokenAction.updateBnb({ bnb: { price: bnbReservePrice } }));

  const categorizePool: TCategorizePool = mappingCategorizePool(
    poolAddressReserve,
    tokens.length
  );

  tokens = tokens.map((token: ITokenDetail, index: number): ITokenDetail => {
    const pancakePoolAddress: IPool[] = mappingPoolAddressReserve(
      DApp.PANCAKE,
      categorizePool.pancake,
      index,
      token.decimals as number,
      bnbReservePrice
    );
    const apePoolAddress: IPool[] = mappingPoolAddressReserve(
      DApp.APE,
      categorizePool.ape,
      index,
      token.decimals as number,
      bnbReservePrice
    );
    const targetPool: IPool = _.maxBy(
      [...pancakePoolAddress, ...apePoolAddress],
      'liquidity'
    ) as IPool;

    return {
      ...token,
      price: targetPool.reservePrice,
      targetPool,
      pools: [...pancakePoolAddress, ...apePoolAddress],
    };
  });

  dispatch(tokenAction.getPool(tokens));
}

async function getChunkPools(
  adressPairs: [string, string][]
): Promise<TPoolAddressReserve> {
  const chunkAddress: [string, string][][] = _.chunk(
    adressPairs,
    CHUNK_DATA_NUMBER
  );
  let chunkPoolAddressPancakePromises: Promise<any>[] = [];
  let chunkPoolAddressApePromises: Promise<any>[] = [];
  let chunkPoolReservePancakePromises: Promise<any>[] = [];
  let chunkPoolReserveApePromises: Promise<any>[] = [];

  _.each(chunkAddress, (address: [string, string][]) => {
    chunkPoolAddressPancakePromises.push(
      bep20Proxy.getPoolAddress(PANCAKE_FACTORY, address)
    );
    chunkPoolAddressApePromises.push(
      bep20Proxy.getPoolAddress(APE_FACTORY, address)
    );
    chunkPoolReservePancakePromises.push(
      bep20Proxy.getReversesMulti(PANCAKE_FACTORY, address)
    );
    chunkPoolReserveApePromises.push(
      bep20Proxy.getReversesMulti(APE_FACTORY, address)
    );
  });

  const chunkDatas = await Promise.all([
    ...chunkPoolAddressPancakePromises,
    ...chunkPoolAddressApePromises,
    ...chunkPoolReservePancakePromises,
    ...chunkPoolReserveApePromises,
    bep20Proxy.getReversesMulti(PANCAKE_FACTORY, [
      [BUSD_BEP20.address, BNB_BEP20.address],
    ]),
  ]);
  const chunkLength: number = (_.size(chunkDatas) - 1) / 4;
  const poolAddressReserve: TPoolAddressReserve = [
    _.flatten(_.slice(chunkDatas, 0, chunkLength) as string[][]),
    _.flatten(_.slice(chunkDatas, chunkLength, chunkLength * 2) as string[][]),
    _.flatten([
      ..._.slice(chunkDatas, chunkLength * 2, chunkLength * 3),
      _.last(chunkDatas),
    ] as [BigNumber, BigNumber][][]),
    _.flatten(
      _.slice(chunkDatas, chunkLength * 3, chunkLength * 4) as [
        BigNumber,
        BigNumber
      ][][]
    ),
  ];

  return poolAddressReserve;
}

function mappingCategorizePool(
  poolAddressReserve: TPoolAddressReserve,
  chunkNumber: number
): TCategorizePool {
  const chunkAddressPancake = _.chunk(poolAddressReserve[0], chunkNumber);
  const chunkAddressApe = _.chunk(poolAddressReserve[1], chunkNumber);
  const chunkReservePancake = _.chunk(poolAddressReserve[2], chunkNumber);
  const chunkReserveApe = _.chunk(poolAddressReserve[3], chunkNumber);

  const categorizePool: TCategorizePool = {
    pancake: {
      busd: {
        address: chunkAddressPancake[0],
        reserve: chunkReservePancake[0],
      },
      usdt: {
        address: chunkAddressPancake[1],
        reserve: chunkReservePancake[1],
      },
      usdc: {
        address: chunkAddressPancake[2],
        reserve: chunkReservePancake[2],
      },
      bnb: {
        address: chunkAddressPancake[3],
        reserve: chunkReservePancake[3],
      },
    },
    ape: {
      busd: {
        address: chunkAddressApe[0],
        reserve: chunkReserveApe[0],
      },
      usdt: {
        address: chunkAddressApe[1],
        reserve: chunkReserveApe[1],
      },
      usdc: {
        address: chunkAddressApe[2],
        reserve: chunkReserveApe[2],
      },
      bnb: {
        address: chunkAddressApe[3],
        reserve: chunkReserveApe[3],
      },
    },
  };

  return categorizePool;
}

function mappingPoolAddressReserve(
  dApp: DApp,
  categorizeAddressReserve: TCategorizeAddressReserve,
  tokenIndex: number,
  tokenDecimals: number,
  bnbReservePrice: number
): IPool[] {
  let pools: IPool[] = [
    {
      dApp,
      address: categorizeAddressReserve.busd.address[tokenIndex].toLowerCase(),
      quoteSymbol: BUSD_BEP20.symbol as string,
      quoteAddress: BUSD_BEP20.address,
      liquidity: tokenHelper.computedLiquidity(
        categorizeAddressReserve.busd.reserve[tokenIndex]
      ),
      reservePrice: tokenHelper.computedTokenReservePricePerQuote(
        categorizeAddressReserve.busd.reserve[tokenIndex],
        tokenDecimals
      ),
      reserve: [
        utils.formatUnits(
          categorizeAddressReserve.busd.reserve[tokenIndex][0],
          tokenDecimals
        ),
        utils.formatUnits(
          categorizeAddressReserve.busd.reserve[tokenIndex][1],
          tokenDecimals
        ),
      ],
    },
    {
      dApp,
      address: categorizeAddressReserve.usdt.address[tokenIndex].toLowerCase(),
      quoteSymbol: USDT_BEP20.symbol as string,
      quoteAddress: USDT_BEP20.address,
      liquidity: tokenHelper.computedLiquidity(
        categorizeAddressReserve.usdt.reserve[tokenIndex]
      ),
      reservePrice: tokenHelper.computedTokenReservePricePerQuote(
        categorizeAddressReserve.usdt.reserve[tokenIndex],
        tokenDecimals
      ),
      reserve: [
        utils.formatUnits(
          categorizeAddressReserve.usdt.reserve[tokenIndex][0],
          tokenDecimals
        ),
        utils.formatUnits(
          categorizeAddressReserve.usdt.reserve[tokenIndex][1],
          tokenDecimals
        ),
      ],
    },
    {
      dApp,
      address: categorizeAddressReserve.usdc.address[tokenIndex].toLowerCase(),
      quoteSymbol: USDC_BEP20.symbol as string,
      quoteAddress: USDC_BEP20.address,
      liquidity: tokenHelper.computedLiquidity(
        categorizeAddressReserve.usdc.reserve[tokenIndex]
      ),
      reservePrice: tokenHelper.computedTokenReservePricePerQuote(
        categorizeAddressReserve.usdc.reserve[tokenIndex],
        tokenDecimals
      ),
      reserve: [
        utils.formatUnits(
          categorizeAddressReserve.usdc.reserve[tokenIndex][0],
          tokenDecimals
        ),
        utils.formatUnits(
          categorizeAddressReserve.usdc.reserve[tokenIndex][1],
          tokenDecimals
        ),
      ],
    },
    {
      dApp,
      address: categorizeAddressReserve.bnb.address[tokenIndex].toLowerCase(),
      quoteSymbol: BNB_BEP20.symbol as string,
      quoteAddress: BNB_BEP20.address,
      liquidity:
        tokenHelper.computedLiquidity(
          categorizeAddressReserve.bnb.reserve[tokenIndex]
        ) * bnbReservePrice,
      reservePrice:
        tokenHelper.computedTokenReservePricePerQuote(
          categorizeAddressReserve.bnb.reserve[tokenIndex],
          tokenDecimals
        ) * bnbReservePrice,
      reserve: [
        utils.formatUnits(
          categorizeAddressReserve.bnb.reserve[tokenIndex][0],
          tokenDecimals
        ),
        utils.formatUnits(
          categorizeAddressReserve.bnb.reserve[tokenIndex][1],
          tokenDecimals
        ),
      ],
    },
  ];

  pools = pools.filter(
    (pool: IPool): boolean => pool.address !== INVALID_ADDRESS
  );

  return pools;
}
